'use strict';

var d3 = require('d3');
var topojson = require('topojson');
var $ = require('jquery');
var _ = require('lodash');

var $container = $('#map');
var svgMap;
var dataTopojson;

function init(datajson) {
    dataTopojson = datajson;
    svgMap = d3.select('#map').append('svg')
        .attr('class', 'svg-map');
};

function render() {
    var w = $container.width(),
        h = $container.height();
    var projection = d3.geo.stereographic()
        .scale(1600)
        .center([-5, 55])
        .translate([w / 2, h / 2])
        .rotate([-20, 0])
        .clipAngle(180 - 1e-4)
        .clipExtent([[0, 0], [w, h]])
        .precision(.1);
    var path = d3.geo.path()
        .projection(projection);

    var countries = svgMap.selectAll('.country').data(topojson.feature(dataTopojson, dataTopojson.objects.countries).features);
    countries.enter().append('path')
        .attr('class', 'country')
        .attr('id', function (country) {
            return country.id;
        })
        .attr('d', path)
        .style('stroke', 'black');
};

module.exports = {
    init: init,
    render: render
};


